#support_div #supportform { /*width: 500px;*/ margin-top: 10px; text-align: center; position: relative; /*inputs*/ /*buttons*/ /*headings*/ }

#support_div #supportform fieldset { background: #fff; border: 0 none; border-radius: 3px; -webkit-box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4); box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4); padding: 20px 30px; -webkit-box-sizing: border-box; box-sizing: border-box; width: 86%; margin: 0 7%; /*stacking fieldsets above each other*/ position: absolute; /*Hide all except first fieldset*/ }

#support_div #supportform fieldset:not(:first-of-type) { display: none; }

