#support_div #supportform .action-button:disabled { opacity: 0.5; }

#support_div #supportform .action-button:hover, #support_div #supportform .action-button:focus { -webkit-box-shadow: 0 0 0 2px #fff, 0 0 0 3px #27ae60; box-shadow: 0 0 0 2px #fff, 0 0 0 3px #27ae60; }

